.blog-wrap {
  max-width: 700px;
  margin: 0 auto;
}

.blog-goBack {
  text-decoration: none;
  font-size: .8rem;
  color: #a9a9a9;
  font-weight: 500;
  margin-bottom: 2rem;
  display: block;
}

.blog-wrap header {
  text-align: center;
}

.blog-date {
  font-size: .8rem;
  color: #a9a9a9;
  font-weight: 500;
}

.blog-wrap img {
  width: 100%;
  border-radius: 10px;
}

.blog-subCategory {
  display: flex;
  justify-content: center;
}

.blog-subCategory div {
  margin: 1rem;
}

.blog-desc {
  padding: 1rem;
  margin-top: 1.5rem;
}

.home-icon {
  font-size: 30px ;
  color: black;
  display: flex;
  align-items: center;
}